import Post from "../post/Post";
import "./feed.css";
import { useEffect, useState } from "react";
import axios from "axios";
import { API_URL } from "../../../../API_URL";
import React from "react";
import Loader from "../loader/Loader";

const Feed = React.memo(({ user, following, type, saved_posts, my_sp }) => {
  const [postlist, setPostList] = useState([]);
  const [loader, setLoader] = useState(false);
  useEffect(() => {
    if (user) {
      const headers = {
        authorization: `Bearer ${
          JSON.parse(localStorage.getItem("greddit_user_loggedin")).token
        }`,
      };
      setLoader(true);
      axios
        .get(API_URL + `/post/${type}/${user}`, { headers })
        .then((resp) => {
          setPostList(resp.data.post);
          // console.log()
          setLoader(false);
        })
        .catch((err) => {
          console.log(err);
          setLoader(false);
        });
    }
  }, [user]);

  const removepost = (post_id) => {
    // console.log(post_id);
    const headers = {
      authorization: `Bearer ${
        JSON.parse(localStorage.getItem("greddit_user_loggedin")).token
      }`,
    };
    setLoader(true);
    axios
      .post(API_URL + `/user/post/${post_id}?op=remove`, {}, { headers })
      .then((resp) => {
        console.log(resp.data);
        setPostList(
          postlist
            .filter((post) => post._id !== post_id)
            ?.slice()
            .reverse()
        );
        setLoader(false);
      })
      .catch((err) => {
        console.log(err);
        setLoader(false);
      });
  };
  return (
    <>
      {loader && <Loader />}
      <div className="feed">
        <div className="feedWrapper">
          {/* <Share /> */}
          {postlist &&
            postlist
              ?.slice()
              .reverse()
              .map((p) => (
                <Post
                  key={Math.random()}
                  post={p}
                  following={following}
                  saved_posts={saved_posts}
                  my_sp={my_sp}
                  removepost={removepost}
                />
              ))}
          {postlist && postlist.length === 0 && (
            <h2 style={{ color: "#848484" }}>No posts yet</h2>
          )}
        </div>
      </div>
    </>
  );
});
export default Feed;
