import Post from "./Sub";
import "./feed.css";
import { useEffect, useState } from "react";
import axios from "axios";
import { API_URL } from "../../../../API_URL";
import React from "react";
import Alert from "@mui/material/Alert";
import Loader from "../loader/Loader";

const MySubs = React.memo(() => {
  const [postlist, setPostList] = useState([]);
  const user = JSON.parse(localStorage.getItem("greddit_user_loggedin")).user
    .user_name;
  const [loader, setLoader] = useState(false);
  useEffect(() => {
    if (user) {
      const headers = {
        authorization: `Bearer ${
          JSON.parse(localStorage.getItem("greddit_user_loggedin")).token
        }`,
      };
      setLoader(true);
      axios
        .get(API_URL + `/subgreddit/`, { headers })
        .then((resp) => {
          setPostList([
            ...resp.data.subgreddit.filter((sub) =>
              sub.followers.includes(user)
            ),
            ...resp.data.subgreddit.filter(
              (sub) => !sub.followers.includes(user)
            ),
          ]);
          setLoader(false);
        })
        .catch((err) => {
          console.log(err);
          setLoader(false);
        });
    }
  }, []);
  const [alert, setAlert] = useState(false);
  const [alertmessage, setAlertMessage] = useState({
    msg: "Updated successfully",
    sev: "success",
  });
  const [attr_list, setAttrList] = useState([
    "name (a)",
    "name (d)",
    "followers",
    "createdAt",
  ]);

  const [selectedattr, setselectedattr] = useState([]);
  const [tagtext, setTagText] = useState("");
  const [searchtext, setSearchText] = useState("");
  const [taglist, setTagList] = useState([]);

  const addTag = (tag) => {
    if (tagtext !== "") {
      setTagList([...taglist, tag]);
    }
  };
  const removeTag = (attr) => {
    setTagList(taglist.filter((att) => attr !== att));
  };

  const addSort = (attr) => {
    if (attr.startsWith("name")) {
      setAttrList(attr_list.filter((att) => !att.startsWith("name")));
    } else {
      setAttrList(attr_list.filter((att) => attr !== att));
    }
    setselectedattr([...selectedattr, attr]);
  };

  const removeSort = (attr) => {
    // setselectedattr(attr);
    if (attr.startsWith("name")) {
      setAttrList([...attr_list, "name (a)", "name (d)"]);
    } else {
      setAttrList([...attr_list, attr]);
    }
    setselectedattr(selectedattr.filter((att) => attr !== att));
  };

  const search = () => {
    let tagstr = taglist.map((tag) => tag.trim()).join(",");
    let attrstr = selectedattr.map((tag) => tag.trim());
    const headers = {
      authorization: `Bearer ${
        JSON.parse(localStorage.getItem("greddit_user_loggedin")).token
      }`,
    };
    let body = {};
    body.tags = tagstr;
    body.sort = attrstr
      .map((attr) => {
        if (attr === "name (a)") {
          return "name";
        } else if (attr === "name (d)") {
          return "-name";
        } else if (attr === "followers") return "-followers";
      })
      .join(",");
    body.search = searchtext;
    setLoader(true);
    axios
      .post(API_URL + `/subgreddit/search`, body, {
        headers: headers,
      })
      .then((resp) => {
        setPostList(resp.data.subgreddit);
        setLoader(false);
      })
      .catch((err) => {
        setAlert(true);
        setAlertMessage({
          msg: `${err.response.status}:${err.response.data.msg}`,
          sev: "error",
        });
        setLoader(false);
        setTimeout(() => {
          setAlert(false);
        }, 3000);
        console.log(err);
      });
  };
  return (
    <>
      {loader && <Loader />}
      <div className="feed">
        <div className="feedWrapper">
          {alert && (
            <Alert severity={alertmessage.sev} sx={{ mt: 3, mb: 2 }}>
              {alertmessage.msg}
            </Alert>
          )}
          {/* <Share /> */}

          <div style={{ backgroundColor: "#eaeaea", margin: "10px" }}>
            <input
              type="text"
              className="search_text"
              placeholder="search..."
              value={searchtext}
              onChange={(e) => {
                setSearchText(e.target.value);
              }}
            />
            <button className="search_button" onClick={search}>
              Search
            </button>
            <input
              type="text"
              className="tag_text"
              placeholder="tag..."
              value={tagtext}
              onChange={(e) => {
                setTagText(e.target.value);
              }}
            />
            <button className="tag_button" onClick={() => addTag(tagtext)}>
              Add tag
            </button>
            <span
              style={{
                fontSize: "20px",
                marginLeft: "10px",
                marginRight: "10px",
                color: "#6a6a6a",
              }}
            >
              sort
            </span>
            {attr_list.map((attr) => {
              return (
                <button
                  className="sort_button"
                  key={Math.random()}
                  onClick={() => addSort(attr)}
                >
                  {attr}
                </button>
              );
            })}
          </div>
          <div>
            {taglist.map((attr) => {
              return (
                <button
                  className="sort_button"
                  key={Math.random()}
                  onClick={() => removeTag(attr)}
                >
                  {attr}
                </button>
              );
            })}
          </div>
          <div>
            {selectedattr.map((attr) => {
              return (
                <button
                  className="sort_button"
                  key={Math.random()}
                  onClick={() => removeSort(attr)}
                >
                  {attr}
                </button>
              );
            })}
          </div>
          <div>
            {postlist &&
              postlist.map((p) => (
                <Post
                  key={Math.random()}
                  sub={p}
                  setAlert={setAlert}
                  setAlertMessage={setAlertMessage}
                  // addPage={addPage}
                />
              ))}
            {postlist && postlist.length === 0 && (
              <h2 style={{ color: "#848484" }}>No Subs yet</h2>
            )}
          </div>
        </div>
      </div>
    </>
  );
});
export default MySubs;
